import {useContext} from 'react';
import { ScrapeContext} from './scrapeContext';

const Data = ()=> {
  const {scrapes} = useContext(ScrapeContext)
  return (<div>
    <h2>Your data</h2>
    <ul>
      {scrapes.map(scrape => <li key={scrape.instagramId}>{scrape.instagramId} - {scrape.nbFollowers} Followers - Already following Dreister: {scrape.isFollowingDreister}</li>)}
    </ul>
  </div>)
}

export default Data;
